<template>
  <div class="showMusicList">
    <div v-for="(item,index) in this.$store.getters.getMusicList" class="father" :key="index">
      <div>
        <span class="iconfont" @click="playMusic(item.id)">&#xe631;</span>
        <span class="musicName" @click="musicDetail(item.id, item.name, item.artists[0].name)">{{ item.name }}</span>
      </div>
      <div class="artists">{{ item.artists[0].name }}</div>
      <div class="album">{{ item.album.name }}</div>
      <div>{{ translateTime(item.duration) }}</div>
    </div>
  </div>
</template>

<script>
import store from '../store';
import { PLAYINGMUSICURL, MUSICCOVERURL, MUSICCOMMENT, SAVEDETAILINFO } from '../store/mutationsTypes';
import axios from 'axios';

export default {
  name: 'ShowMusicList',
  data() {
    return {
      cover: '',
    }
  },
  methods: {
    translateTime: function(stamp) {
      let minute = Math.floor(stamp/60000);
      minute = minute >= 10 ? minute : '0' + minute;
      let second = Math.floor(stamp / 1000 % 60);
      second = second >= 10 ? second : '0' + second;
      return minute + ':' + second;
    },
    playMusic: function(musicId) {
      axios.get("https://autumnfish.cn/song/url?id=" + musicId)
        .then((res) => {
            if(res.data.data[0].url == null) alert('暂无歌曲...');
            this.$store.commit(PLAYINGMUSICURL, res.data.data[0].url);
        }, (err) => {
            console.log(err);
        })
    },
    musicDetail: function(musicId, detailName, detailArtistName) {
      let that = this;
      console.log(musicId, detailName, detailArtistName);
      axios.all([this.getMusicDetail(musicId), this.getMusicCommetn(musicId)])
        .then(axios.spread(function(resMusicDetail, resMusicComment) {
          console.log(resMusicDetail);
          console.log(resMusicComment);
          that.$store.commit(MUSICCOVERURL ,resMusicDetail.data.songs[0].al.picUrl);
          that.$store.commit(MUSICCOMMENT ,resMusicComment.data.hotComments);
          that.$store.commit(SAVEDETAILINFO, {
            detailid: musicId,
            detailname: detailName,
            detailartistname: detailArtistName
          })
          // that.$router.push('/home/musicdetail');
          console.log('到这里了吗？')
          console.log(that)
          that.$router.push({
            name: 'music-detail'
            // name: 'home'
          });
          console.log('到这里了吗？2')
        }))
        .catch(err => {
          console.log(err);
        })
    },
    getMusicDetail(musicId) {
      return axios.get("https://autumnfish.cn/song/detail?ids=" + musicId);
    },
    getMusicCommetn(musicId) {
      return axios.get("https://autumnfish.cn/comment/hot?type=0&id=" + musicId);
    }
  },
  mounted() {
    console.log('...');
    console.log(this);
    if(this.$store.getters.getMusicList.length === 0) {
      this.$router.push('/');
    }
  }
}
</script>

<style scoped>
  @import '../assets/css/showMusicList.css';
</style>
