export default {
    getMusicList(state) {
        return state.musicList;
    },
    getPlayingMusicURL(state) {
        return state.playingMusicURL;
    },
    getMusicCoverURL(state) {
        return state.musicCoverURL;
    },
    getMusicComment(state) {
        return state.musicComment;
    },
    getDetailInfo(state) {
        return {
            detailId: state.detailId,
            detailName: state.detailName,
            detailArtistName: state.detailArtistName
        }
    }
}