import { MUSICLIST, PLAYINGMUSICURL, MUSICCOVERURL, MUSICCOMMENT, SAVEDETAILINFO } from './mutationsTypes';

export default {
    [MUSICLIST](state, payload) {
        state.musicList = payload;
    },
    [PLAYINGMUSICURL](state, payload) {
        state.playingMusicURL = payload;
    },
    [MUSICCOVERURL](state, payload) {
        state.musicCoverURL = payload;
    },
    [MUSICCOMMENT](state, payload) {
        state.musicComment = payload;
    },
    [SAVEDETAILINFO](state, payload) {
        state.detailId = payload.detailid;
        state.detailName = payload.detailname;
        state.detailArtistName = payload.detailartistname;
    }
    
}