<template>
    <div>
      <div class="cover">
        <span @click="back" class="back"><span class="iconfont">&#xe611;</span></span>
        <img :src="this.$store.getters.getMusicCoverURL" alt="cover" class="coverImg">
        <img src="http://xhxhxh.ltd/music/img/coverall.bfbc468f.png" alt="coverall" class="coverall">
        <div class="showBar">
          <p @click="play($store.getters.getDetailInfo.detailId)">播放<span class="iconfont">&#xe631;</span></p>
          <p>歌名：{{this.$store.getters.getDetailInfo.detailName}}</p>
          <p>歌手：{{this.$store.getters.getDetailInfo.detailArtistName}}</p>
        </div>
      </div>
      <div class="comments">
        <span v-if="$store.getters.getMusicComment.length == 0" class="noComment">暂无评论</span>
        <div v-for="(item, index) in this.$store.getters.getMusicComment" :key="index" class="comment">
          <div class="leftPart">
            <img :src="item.user.avatarUrl" alt="avatar">
          </div>
          <div class="rightPart">
            <p>
              <span>{{item.user.nickname}}:</span> {{item.content}}
            </p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import axios from 'axios';
import { PLAYINGMUSICURL } from '../store/mutationsTypes';

export default {
  name: 'SearchBar',
  data() {
    return {
      
    }
  },
  methods: {
   play(musicId) {
      axios.get("https://autumnfish.cn/song/url?id=" + musicId)
        .then((res) => {
          console.log(this);
            if(res.data.data[0].url == null) alert('暂无歌曲...');
            this.$store.commit(PLAYINGMUSICURL, res.data.data[0].url);
        }, (err) => {
            console.log(err);
        });
    },
    back: function() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/')
    }
  },
  mounted() {
    console.log('...');
    if(this.$store.getters.getMusicList.length === 0) {
      this.$router.push('/');
    }
  }
}
</script>

<style scoped>
  @import '../assets/css/showMusicDetail.css';
</style>
