<template>
    <div id="searchBar">
        <span @click="goHome"><span class="iconfont">&#xe679;</span></span>
        <input type="text" v-model="searchContent" @keyup.enter="search" placeholder="搜索音乐，歌手">
    </div>
</template>

<script>
import { MUSICLIST } from '../store/mutationsTypes';
import axios from 'axios';

export default {
  name: 'SearchBar',
  data() {
    return {
      searchContent: '',
    }
  },
  methods: {
    search: function() {
      axios.get("https://autumnfish.cn/search?keywords=" + this.searchContent)
        .then((res) => {
          this.$store.commit(MUSICLIST, res.data.result.songs);
          console.log(res.data.result.songs);
          // this.$router.push('/musiclist');
          this.$router.push('/home/musiclist');
        }, (err) => {
          console.log(err);
        });
    },
    goHome() {
      this.$router.push('/home');
    }
  }
}
</script>

<style scoped>
  @import '../assets/css/searchBar.css';
</style>
