import Vue from 'vue';
import VueRouter from 'vue-router';

const Home = () => import('components/Home');
const ShowMusicList = () => import('components/ShowMusicList');
// const ShowMusicDetail = () => import('components/ShowMusicDetail');

// import Home from 'components/Home';
// import ShowMusicList from 'components/ShowMusicList';
import ShowMusicDetail from 'components/ShowMusicDetail';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        redirect: '/home'
    },
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    {
        path: '/home/musiclist',
        name: 'music-list',
        component: ShowMusicList
    },
    {
        path: '/home/musicdetail',
        name: 'music-detail',
        component: ShowMusicDetail
    }
]

const router = new VueRouter({
    routes,
    mode: 'history'
});

export default router;