<template>
  <div class="player">
    <audio :src="this.$store.getters.getPlayingMusicURL" autoplay controls id="myAudio"></audio>
  </div>
</template>

<script>
export default {
  name: 'Player',
  data() {
    return {
      musicURL: this.$store.getters.getPlayingMusicURL
    }
  },
  methods: {
    setVolume() {
      // 设置默认音量
      document.querySelector('#myAudio').volume = 0.2;
    }
  },
  mounted() {
    this.setVolume();
  }
}
</script>

<style scoped>
  #myAudio {
    margin-top: 10px;
    width: 1024px;
    height: 40px;
    outline: none;
  }
</style>
